import Image from "next/image";

export default function Navbar() {
    return (
        <nav className="navbar">
            <Image
                src="/LOGO-UEYE.svg"
                alt="uEye Logo"
                width={52}
                height={52}
                className="logo"
            />
            <button className="demo-button">
                Receba a demo
            </button>
        </nav>
    )
}